@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Roboto);
:root{
  /* Colors */
  --highlight-color: #FCDAB7;
  --light-1-color: #1E5F74;
  --light-2-color: #133B5C;
  --dark-color: #1D2D50;
  /*  */
  --gutter: 15px;
  /* Components */
  --task-height: 50px;
  /* */
  --shadow: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

body {
  color: #FCDAB7;
  color: var(--highlight-color);
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.App{
    background-color: var(--light-1-color);
    min-height: calc(100vh - var(--gutter) );
    padding-top: 0;
  }
  
.App-Container{
    padding: var(--gutter);
}

.App-SVG{
  max-height: 200px;
  -webkit-filter: var(--shadow);
          filter: var(--shadow);
}

.App-Empty{
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.Heading{
    position: sticky;
    position: -webkit-sticky;

    background-color: var(--light-1-color);
    
    padding: 1em var(--gutter);
    margin-bottom: 0;
    top: 0;
    
    z-index: 99;
}
.Heading > .Heading-List{
    font-size: 2em;
    font-weight: bold;
    margin: 0;
}

.Heading > .Heading-Date{
    font-weight: bold;
    margin: 0;
}
.TaskInput{
    /* Positioning */
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    /* Display */
    display: flex;
    justify-content: left;
    align-items: center;
    /* Colors */
    background-color: var(--light-1-color);
    /* Margin, Border, Padding */
    padding: var(--gutter);
}            

.TaskInput-Form{
    /* Sizeing */
    min-height: var(--task-height);
    width: 100%;
    /* Display */
    display: flex;
    align-items: center;
    /* Colors */
    background-color: var(--light-2-color);
    /* Margin, Border, Padding */
    border-radius: 5px;
    padding: 0 15px;
    /* Others */
    -webkit-filter: var(--shadow);
            filter: var(--shadow);
}

.TaskInput-Input{
    /* Sizeing */
    height: auto;
    /* Display */
    display: inline;
    flex: 1 1;
    /* Font */
    font: inherit;
    font-size: 16px;
    /* Margin, Border, Padding */
    border: 0;
    padding: 0;
    /* Colors */
    color: var(--highlight-color);
    background-color: transparent;
}

.TaskInput-Input:focus {
    outline: none;
}

.TaskInput-Input::-webkit-input-placeholder{
    color: var(--highlight-color);
}

.TaskInput-Input:-ms-input-placeholder{
    color: var(--highlight-color);
}

.TaskInput-Input::placeholder{
    color: var(--highlight-color);
}


.TaskInput-Form svg{
    width: 22px;
    height: 22px;
    padding-right: 10px;
}
.TaskList{
    padding-bottom: calc(
        ( var(--gutter) * 2)
        + var(--task-height)
    );
}
.Task{
    /* Sizeing */
    min-height: var(--task-height);
    /* Display */
    display: flex;
    align-items: center;
    justify-content: left;
    /* Colors */
    background-color: var(--light-2-color);
    /* Margin, Border, Padding */
    border-radius: 5px;
    margin-bottom: .5em;
    padding-left: 15px;
    /* Others */
    -webkit-filter: var(--shadow);
            filter: var(--shadow);
}

.Task-Text{
    display: inline;
    color: var(--highlight-color);
    font-size: 16px;
}

.Task svg{
    width: 22px;
    height: 22px;
    padding-right: 10px;
}

.Task-Completed{
    font-style: italic;
    text-decoration: line-through;
}
